.screenSaver {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  font-size: 2em;
}
.screenSaver.open {
  display: block;
  -webkit-animation: vanishIn 0.5s 1;
          animation: vanishIn 0.5s 1;
}
.screenSaver .centralised {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}



